// --------------------------------------------------
//		Tools: margins [placeholder]
// --------------------------------------------------

@use 'settings' as *;
@use 'breakpoints' as *;
@use 'clamped' as *;
@use 'sass:map';
@use 'sass:list';

// Block paddings vertical ========================================

@each $size, $value in $clamp-y-spaces {
    @if $size != 'none' {
        // Margin top
        %pt-#{$size} {
            padding-top: clamped(
                list.nth($value, 1) * 1px,
                list.nth($value, 2) * 1px,
                390px,
                991px
            );

            @include up(md) {
                padding-top: clamped(
                    list.nth($value, 3) * 1px,
                    list.nth($value, 4) * 1px,
                    992px,
                    1920px
                );
            }
        }

        // Margin bottom
        %pb-#{$size} {
            padding-bottom: clamped(
                list.nth($value, 1) * 1px,
                list.nth($value, 2) * 1px,
                390px,
                991px
            );

            @include up(md) {
                padding-bottom: clamped(
                    list.nth($value, 3) * 1px,
                    list.nth($value, 4) * 1px,
                    992px,
                    1920px
                );
            }
        }

        // Margin vertical
        %ptb-#{$size} {
            padding-top: clamped(
                list.nth($value, 1) * 1px,
                list.nth($value, 2) * 1px,
                390px,
                991px
            );
            padding-bottom: clamped(
                list.nth($value, 1) * 1px,
                list.nth($value, 2) * 1px,
                390px,
                991px
            );

            @include up(md) {
                padding-top: clamped(
                    list.nth($value, 3) * 1px,
                    list.nth($value, 4) * 1px,
                    992px,
                    1920px
                );
                padding-bottom: clamped(
                    list.nth($value, 3) * 1px,
                    list.nth($value, 4) * 1px,
                    992px,
                    1920px
                );
            }
        }
    }
}

